<template>
  <div>
    <BulletinBoard class="mb-2" style="min-height: 110px" :Bulletin="$store.state.bulletin.list" interval="5000" :loading="$store.state.bulletin.loading" />
    <div class="row">
      <div class="col-lg-4 px-2">
        
      </div>
      <div class="col-lg-8 px-2">
        <div class="card">
          <div class="card-body">
            <div class="card-title">
              <h4 data-v-73027a57="" class="card-title text-secondary fw-bolder mb-0">
                <font-awesome-icon :icon="['fas', 'money-bill']"/>
                快速充值 </h4>
            </div>
            <hr />
            <xz-checks v-model="select.pay_type" class="mb-3">
              <xz-option :value="0">
                <Card color="success" left>
                  <template #icon>
                    <img :src="Wechat_pay" alt="" width="40" height="40">
                  </template>
                  <h3 class="font-weight-bolder mb-0 text-nowrap ">
                    微信支付
                  </h3>
                </Card>
              </xz-option>
              <xz-option :value="1">
                
                <Card color="info" left>
                    <template #icon>
                      <img :src="Alipay" alt="" width="40" height="40">
                    </template>
                    <h3 class="font-weight-bolder mb-0 text-nowrap ">
                      支付宝
                    </h3>
                </Card>
              </xz-option>
            </xz-checks>

            <h4 class="ps-3 font-weight-bolder border-left-primary border-left-width-3 text-nowrap mb-3">固定金额充值</h4>
            <xz-checks v-model="select.amount">
              <xz-option :value="0">
                <div class=" check-card normal-check-card">
                  <h3 class="font-weight-bolder mb-0 text-nowrap">
                    ￥ 5.00
                  </h3>
                </div>
              </xz-option>
              <xz-option :value="1">
                <div class=" check-card normal-check-card">
                  <h3 class="font-weight-bolder mb-0 text-nowrap">
                    ￥ 10.00
                  </h3>
                </div>
              </xz-option>
              <xz-option :value="2">
                <div class=" check-card normal-check-card">
                  <h3 class="font-weight-bolder mb-0 text-nowrap">
                    ￥ 50.00
                  </h3>
                </div>
              </xz-option>
              <xz-option :value="3">
                <div class=" check-card normal-check-card">
                  <h3 class="font-weight-bolder mb-0 text-nowrap">
                    ￥ 100.00
                  </h3>
                </div>
              </xz-option>
              <xz-option :value="4">
                <div class=" check-card normal-check-card">
                  <h3 class="font-weight-bolder mb-0 text-nowrap">
                    ￥ 200.00
                  </h3>
                </div>
              </xz-option>
            </xz-checks>

            <button v-wave class="btn btn-success btn-relief w-100" @click="pay()">立即充值</button>

            <h4 class="ps-3 font-weight-bolder border-left-primary border-left-width-3 text-nowrap my-3">自定义金额充值</h4>
            <div class="d-flex">
              <el-input v-model="input.amount" placeholder="请输入充值金额"></el-input>
              <el-button v-wave class="ms-2" @click="pay(input.amount)">立即充值</el-button>
            </div>

          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Card from "@/components/Card.vue";
import XzChecks from "@/components/XzChecks.vue";
import XzOption from "@/components/XzOption.vue";

import Wechat_pay from "@/assets/img/pay/wechat_pay.png";
import Alipay from "@/assets/img/pay/alipay.png";

import { toPay } from "@/scripts/pay/pay.js";

export default {
  name: "DashboardExpensePayPage",
  components: {
    Card,
    XzOption,
    XzChecks
  },
  data() {
    return {
      select: {
        pay_type: 0,
        amount: 0
      },
      input: {
        amount: null
      },
      Wechat_pay,
      Alipay
    };
  },
  methods: {
    async pay(custom_money) {
      const pay_type = this.select.pay_type === 0 ? "wxpay" : "alipay";
      let money = 0;
      switch (this.select.amount) {
        case 0:
          money = 5;
          break;
        case 1:
          money = 10;
          break;
        case 2:
          money = 50;
          break;
        case 3:
          money = 100;
          break;
        case 4:
          money = 200;
          break;
        default:
          break;
      }
      money = custom_money > 0 ? custom_money : money;

      if (money <= 0) {
        this.$toastx({ type: "danger", title: "请选择或输入充值金额" });
        return;
      }

      const resp = await toPay(pay_type, money);

      if (resp.code !== 1) {
        this.$swal({ icon: "error", title: "初始化支付失败", text: resp.msg });
        return;
      } else {
        this.$swal({
          icon: "question",
          title: "您是否已完成支付",
          showCancelButton: true,
          confirmButtonText: "确认",
          cancelButtonText: "取消",
        }).then((result) => {
          
        })
      }
    }
  }
};
</script>

<style>
.title {
  font-size: 30px;
}
</style>
