import store from "@/store";
import request from "@/scripts/utils/request";

export async function toPay(type, money) {
    let resp = {};
    try {
        const data = await request({
            method: "post",
            url: "/expense/pay",
            data: {
                type,
                money
            }
        });

        resp = data.data;
    } catch (e) {
        resp = e.data;
    }

    if (resp.code === 1) {
        if (resp.payurl) {
            window.open(resp.payurl);
        }
    }

    return resp;
}